import { Component, h, Element, State, Host } from '@stencil/core';
import { ICellRendererComp, ICellRendererParams } from 'ag-grid-community';
import { ITooltipCell } from '../../../../types';

export interface TextBadgeCellData {
    text: string;
    badgeText: string;
    tooltip?: string;
}

export interface ITextBadgeCellParams
    extends ICellRendererParams,
        ITooltipCell {
    text?: (data: any) => string;
    badgeText?: (data: any) => string;
    badgeType?: string;
}

/**
 * @category Table Cell
 * @description A cell component for displaying a text with a badge.
 */

@Component({
    tag: 'atui-text-badge-cell',
    shadow: false,
})
export class AtuiTextBadgeCell implements ICellRendererComp {
    @Element() el: HTMLElement;
    @State() textValue: string;
    @State() badgeTextValue: string;
    @State() params: ITextBadgeCellParams;

    init(params: ICellRendererParams): void {
        this.params = params as ITextBadgeCellParams;

        if (this.params.text) {
            this.textValue = this.params.text(params.data);
        } else {
            this.textValue = params.value?.text || '';
        }

        if (this.params.badgeText) {
            this.badgeTextValue = this.params.badgeText(params.data);
        } else {
            this.badgeTextValue = params.value?.badgeText || '';
        }
    }

    getGui(): HTMLElement {
        return this.el;
    }

    refresh(params: ICellRendererParams): boolean {
        this.updateCell(String(params.value));
        return true;
    }

    private updateCell(newValue: string) {
        this.params.value = newValue;
        this.textValue = this.params.value;
    }

    render() {
        return (
            <Host class="flex h-full items-center">
                <atui-tooltip
                    position="right"
                    disabled={!this.params.generateTooltip}
                >
                    <div slot="tooltip-trigger">
                        <span class="truncate">{this.textValue}</span>
                        <atui-badge
                            type="info"
                            class="ml-4"
                            label={this.badgeTextValue}
                        />
                    </div>
                    {this.params?.generateTooltip && (
                        <span class="leading-normal">
                            {this.params.generateTooltip(this.params)}
                        </span>
                    )}
                </atui-tooltip>
            </Host>
        );
    }
}
